.list {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  justify-content: center;
}

.item {
  box-shadow: 0px 2px 4px -1px rgba(248, 246, 246, 0.2),
    0px 4px 5px 0px rgba(237, 233, 233, 0.14),
    0px 1px 10px 0px rgba(255, 255, 255, 0.12);
}

.item:hover,
.item:focus {
  box-shadow: 0px 7px 8px -5px rgba(247, 246, 246, 0.2),
    0px 8px 9px 0px rgba(249, 248, 248, 0.14),
    0px 5px 15px 0px rgba(247, 245, 245, 0.12);
}
.link__movies {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.link__movies:hover,
.link__movies:focus {
  color: red;
}

.description {
  font-size: 14px;
  padding: 10px;
}
